@use "global" as *;

.swiper-container {
  width: 100%; /* スライダーの幅を調整 */
  height: 100%; /* スライダーの高さを調整 */
}
.swiper-container {
  transform: translate3d(0, 0, 0);
}


/* スライド要素 */
.swiper-slide {
  text-align: center;
  // width: 100%; /* スライドの幅を調整 */
  // height: 100%; /* スライドの高さを調整 */
  display: flex; /* スライド内の要素を横並びに配置する場合 */
  align-items: center; /* スライド内の要素を垂直方向に中央揃えする場合 */
  justify-content:center; /* スライド内の要素を水平方向に中央揃えする場合 */
  @include mq (xl){
    width: rem(330);
  }
}
.swiper-wrapper.campaign{
  margin-left: rem(173+547.5);
}

